@import url(https://fonts.googleapis.com/css?family=Khula:700);
body {
  margin: 0;
  /*font-family: "Josefin Sans", sans-serif;*/
  font-family: "Texturina", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  font-size: 16px;
  background-color: #fff;
  /* text-align: center;
	display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; */
}

.all-content {
  position: absolute;
  left: 0px;
  top: 0px;
  right: 0px;
  bottom: 0px;
}

.main-column1 {
  position: relative;
  width: 70%;
  height: 100%;
  float: left;
}

.main-column2 {
  position: relative;
  width: 30%;
  height: 100%;
  float: right;
}

.main-column .editorr {
  max-width: 800px;
  text-align: center;
}

.main-column .newvideo {
  overflow: scroll;
  max-width: 600px;
}
.hidden {
  opacity: 0;
}
.console-container {
  font-size: 3em;
  display: block;
  color: white;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.console-underscore {
  display: inline-block;
  position: relative;
  top: -0.14em;
  left: 10px;
}

